import { safeJsonParse } from '@pi-xcels/utils';
import { defineStore } from 'pinia';
import { ref, watch } from 'vue';

export interface Filter {
  sortBy: null | 'print_at' | 'total_price';
  orderBy: null | 'desc' | 'asc';
  date: string[] | null[];
  store: string[];
}

export const getDefaultFilter = (): Filter => ({
  orderBy: null,
  sortBy: null,
  date: [null, null],
  store: [],
});

export const useFilterStore = defineStore('filter', () => {
  const filter = ref<Filter>(getDefaultFilter());

  if (sessionStorage.getItem('filter')) {
    filter.value = safeJsonParse<Filter>(sessionStorage.getItem('filter') as string) || filter.value;
  }

  watch(filter, (filter) => {
    const filterEncoded = JSON.stringify(filter);
    if (filterEncoded !== sessionStorage.getItem('filter')) {
      sessionStorage.setItem('filter', filterEncoded);
    }
  });

  return {
    filter,
  };
});
