<template>
  <div class="flex flex-col justify-center pt-5 pb-20">
    <div class="flex items-center w-10/12 mx-auto relative">
      <div class="mx-auto w-full">
        <h3 class="text-xl font-poppins font-semibold text-center" data-cy="homepage-title">
          {{ $t('MyReceipts.Title') }}
        </h3>
      </div>
      <div v-wave v-if="!featureState.demoMode" class="absolute right-0">
        <font-awesome-icon
          v-if="
            !filterState.filter.orderBy &&
            !filterState.filter.sortBy &&
            !filterState.filter.date[0] &&
            !filterState.filter.date[1] &&
            filterState.filter.store?.length === 0
          "
          icon="fa-solid fa-filter"
          class="text-xl ml-auto text-black"
          @click="toggleSortFilter"
          data-cy="filter-icon"
        />
        <font-awesome-icon v-else icon="fa-solid fa-filter" class="text-xl ml-auto text-green-500" @click="toggleSortFilter" data-cy="filter-icon" />
      </div>
    </div>
    <div class="w-11/12 mx-auto pb-5 pt-7" v-if="isLoadingUserReceipt && !isFetchingNextPage && receipts.length === 0">
      <div class="bg-white w-full rounded-xl p-4 h-20 flex flex-row justify-between mb-5 drop-shadow-md" data-cy="receipt-skeleton">
        <div class="h-full flex flex-row">
          <div class="w-12 h-12 animate-pulse bg-gray-300 rounded-full"></div>
          <div class="w-auto h-full ml-3 flex flex-col justify-around">
            <div class="w-24 h-5 animate-pulse bg-gray-300 rounded-md"></div>
            <div class="w-16 h-5 animate-pulse bg-gray-300 rounded-md"></div>
          </div>
        </div>
        <div class="text-right flex flex-col justify-around items-end">
          <div class="w-16 h-4 animate-pulse bg-gray-300 rounded-md"></div>
          <div class="w-28 h-6 animate-pulse bg-gray-300 rounded-md"></div>
        </div>
      </div>
    </div>
    <div class="w-11/12 mx-auto pb-5 pt-7" v-else-if="receipts.length > 0" data-cy="receipts-container">
      <DynamicScroller :items="receipts" :min-item-size="54" class="h-full w-full" key-field="id">
        <template v-slot="{ item, index, active }">
          <DynamicScrollerItem class="flex flex-col mx-1" :item="item" :active="active" :data-index="index">
            <div class="ml-5 mb-2" data-cy="date-group">
              {{ groupReceiptsByDate(item.receipt, receipts[index - 1]?.receipt) }}
            </div>
            <div
              @click="showReceipt({ data: item, viewOnly: true })"
              class="cursor-pointer bg-white w-full rounded-xl p-4 h-20 flex flex-row justify-between mb-5 drop-shadow-md"
              data-cy="receipt-data"
            >
              <div class="h-full flex flex-row">
                <div class="w-auto h-full max-w-[30%]" data-cy="merchant-logo">
                  <img :src="getCompanyLogoURL(item.companies?.logos)" class="w-auto h-full object-contain" :alt="item.receipt.Store?.CompanyName" />
                </div>
                <div class="w-auto h-full ml-3 flex flex-col justify-around">
                  <p class="gfold:text-sm text-base font-medium" data-cy="merchant-name">
                    {{ item.receipt.Store?.CompanyName === 'PT. FAJAR MITRA INDAH' ? 'FamilyMart' : item.receipt.Store?.CompanyName }}
                  </p>
                  <p class="gfold:text-sm text-base text-neutral-400" data-cy="receipt-timestamp">
                    {{ customTimeStamp(item?.receipt?.PrintAt ?? item?.receipt?.ReprintAt ?? null, true) }}
                  </p>
                </div>
              </div>
              <div class="text-right flex flex-col justify-around">
                <p class="gfold:text-xs text-sm font-medium w-max ml-auto" data-cy="store-name">
                  {{ customStoreName(item.receipt.Store) }}
                </p>

                <p class="gfold:text-base text-xl font-medium w-max ml-auto" data-cy="receipt-total-price" v-if="item.receipt.Transaction.TotalPrice">
                  {{ printTotalPrice(item.companies.locale, item.receipt.CurrencyCode || item.companies.currencyCode, item.receipt.Transaction.TotalPrice) }}
                </p>
              </div>
            </div>
          </DynamicScrollerItem>
        </template>
      </DynamicScroller>
      <div class="mx-auto w-full flex" v-if="isFetchingNextPage">
        <div class="mx-auto rounded-xl flex flex-row" data-cy="receipt-loader-skeleton">
          <div class="w-3 h-3 animate-pulse bg-gray-500 rounded-full mx-2"></div>
          <div class="w-3 h-3 animate-pulse bg-gray-500 rounded-full mx-2"></div>
          <div class="w-3 h-3 animate-pulse bg-gray-500 rounded-full mx-2"></div>
        </div>
      </div>
    </div>
    <div
      class="w-11/12 mx-auto pb-5 pt-7"
      v-else-if="
        (filterState.filter.orderBy || filterState.filter.sortBy || filterState.filter.date[0] || filterState.filter.date[1]) && filterState.filter.store.length === 0
      "
    >
      <p class="text-center text-lg px-4 italic" data-cy="empty-filter-placeholder">{{ $t(`MyReceipts.EmptyFilter`) }}</p>
    </div>
    <div class="w-11/12 mx-auto pb-5 pt-7" v-else-if="receipts.length == 0 && !isLoadingUserReceipt">
      <p class="text-center text-lg px-4 italic" data-cy="empty-receipt-placeholder">
        {{ $t('MyReceipts.EmptyInfo') }}
      </p>
    </div>

    <div v-if="!hasNextPage && receipts.length > 0 && receipts.length > limit" class="flex justify-center pb-5 text-gray-400" data-cy="end-of-receipts">
      {{ $t('MyReceipts.EndOfReceipts') }}
    </div>
    <div ref="infiniteScrollTrigger" class="h-2.5"></div>
  </div>

  <SortFilter :sortFilterOpen="sortFilterOpen" :filter="filterState.filter" :availStore="availStore" @toggle-sort-filter="toggleSortFilter" @apply="handleFilter" />
</template>

<script setup lang="ts">
import { useUserReceipt } from '@/api/receipt';
import { useUserReceiptStore } from '@/api/user';
import { useAlertStore } from '@/stores/alert';
import { useFeatureStore } from '@/stores/feature';
import { Filter, useFilterStore } from '@/stores/filter';
import { useAuthManager } from '@/utils/auth';
import { useModalErrorMsg } from '@/utils/hooks/useErrorHandling';
import { GetReceiptQuery, UserReceiptsQuery } from '@pi-xcels/graphql/types';
import { formatPrice } from '@pi-xcels/utils';
import * as Sentry from '@sentry/vue';
import { computed, onMounted, onUnmounted, readonly, ref, watch } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRouter } from 'vue-router';
import { useSubscribeToReceiptChanges } from './hooks/useSubscribeToReceiptChanges';
import { getCompanyLogoURL } from '@/utils/receipt';

const { t, d } = useI18n();

const router = useRouter();
const featureState = useFeatureStore();
const filterState = useFilterStore();
const alertState = useAlertStore();
const { isAuthenticated } = useAuthManager();
const { data: userReceiptStore } = useUserReceiptStore({
  enabled: isAuthenticated,
});

const availStore = computed(() => userReceiptStore.value?.userReceiptsStore ?? []);

const sortFilterOpen = ref(false);

const limit = computed(() => Math.ceil(window.innerHeight / 100));

const {
  data,
  isLoading: isLoadingUserReceipt,
  hasNextPage,
  fetchNextPage,
  isFetchingNextPage,
  error: userReceiptError,
} = useUserReceipt(
  computed(() => ({
    limit: limit.value ?? 7,
    filter: filterState.filter,
  })),
  {
    enabled: isAuthenticated,
  },
  { fetchPolicy: 'no-cache', nextFetchPolicy: 'cache-first' }
);

useModalErrorMsg(
  userReceiptError,
  readonly(
    ref({
      id: 'user-receipt-error',
      title: t('Error.UserReceiptsQueryFails.Title'),
      body: t('Error.UserReceiptsQueryFails.Body'),
      btnTxt: t('Error.BtnTextOK'),
      btnAction() {
        alertState.removeAlertMessage('user-receipt-error');
      },
    })
  )
);

const receipts = computed(() => {
  return data.value?.pages?.map((page) => page.userReceipts ?? []).flat() ?? [];
});

// log if receipts has duplicate
watch(receipts, (receipts) => {
  const receiptIds = receipts.map((receipt) => receipt.id);
  const receiptIdsWithDupes = [...new Set(receiptIds.filter((id) => receiptIds.indexOf(id) !== receiptIds.lastIndexOf(id)))];
  if (receiptIdsWithDupes.length) {
    console.warn('Duplicated receipt found', [...new Set(receiptIds.filter((id) => receiptIds.indexOf(id) !== receiptIds.lastIndexOf(id)))]);
    Sentry.captureMessage(`Duplicated receipt found: ${receiptIdsWithDupes.join(', ')}`);
  }
});

const showReceipt = ({
  data,
  isNewReceipt = false,
  viewOnly = false,
}: {
  data: NonNullable<UserReceiptsQuery['userReceipts']>[number] | NonNullable<GetReceiptQuery['getReceipt']>;
  isNewReceipt?: boolean;
  viewOnly?: boolean;
}) => {
  if (isNewReceipt) {
    alertState.addToastMsg({
      id: t('SaveReceipt.SaveInfo') + data.transactionRef,
      title: t('SaveReceipt.SaveInfo'),
      autoCloseInMs: 5000,
    });
  }

  const hash = data.transactionRef;
  if (!hash) {
    throw new Error('Receipt hash is missing');
  }

  router.push({
    path: '/receipt',
    query: {
      v: hash,
      ...(viewOnly && { mode: 'view' }),
    },
  });

  window.scrollTo(0, 0);
};

const customizeText = (str: string) => {
  return str
    .split(' ')
    .map((item) => item.charAt(0).toUpperCase() + item.slice(1).toLowerCase())
    .join(' ');
};

const customStoreName = (store: any) => {
  const storeName = store?.StoreName ?? 'Store';
  return storeName.split(' ').length <= 2 ? customizeText(storeName) : customizeText(`${storeName.substring(0, 17).trim()}...`);
};

const customTimeStamp = (timeStamp: any, format: boolean) => {
  if (!timeStamp) {
    return 'PrintTime';
  }

  let formattedTimeStamp = timeStamp;

  if (format) {
    formattedTimeStamp = `${timeStamp.split(' ')[0].split('/').reverse().join('/')} ${timeStamp.split(' ')[1]}`;
  }

  return d(new Date(formattedTimeStamp), {
    hour: '2-digit',
    minute: '2-digit',
    hour12: true,
  });
};

const groupReceiptsByDate = (receiptNow: any, receiptPrev: any) => {
  // Receipt Time Stamp = DD/MM/YYYY HH:MM
  const receiptTimeStampNow = receiptNow?.PrintAt ?? receiptNow?.ReprintAt ?? null;
  const receiptTimeStampPrev = receiptPrev?.PrintAt ?? receiptPrev?.ReprintAt ?? null;

  //Formatted Time Stamp = YYYY/MM/DD
  const formattedTimeStampNow = receiptTimeStampNow?.split(' ')[0].split('/').reverse().join('/') ?? null;
  const formattedTimeStampPrev = receiptTimeStampPrev?.split(' ')[0].split('/').reverse().join('/') ?? null;

  const date = new Date();
  const currentReceiptDate = new Date(formattedTimeStampNow);
  const yesterdayDate = new Date();
  yesterdayDate.setDate(yesterdayDate.getDate() - 1);

  switch (true) {
    case formattedTimeStampNow === formattedTimeStampPrev:
      return null;

    case currentReceiptDate.toDateString() === date.toDateString():
      return t('Receipt.Today');

    case yesterdayDate.toDateString() === currentReceiptDate.toDateString():
      return t('Receipt.Yesterday');

    case date.getFullYear().toString() === formattedTimeStampNow.split('/')[0]:
      return d(currentReceiptDate, 'short');

    case date.getFullYear().toString() !== formattedTimeStampNow.split('/')[0]:
      return d(currentReceiptDate, 'long');

    default:
      return null;
  }
};

const infiniteScrollTrigger = ref(null);
const observer = new IntersectionObserver((entries) => {
  entries.forEach((entry: IntersectionObserverEntry) => {
    if (entry.isIntersecting) {
      fetchNextPage();
    }
  });
});

onMounted(() => {
  const targetElement = infiniteScrollTrigger.value;
  if (targetElement) {
    observer.observe(targetElement);
  }
});

onUnmounted(() => {
  const targetElement = infiniteScrollTrigger.value;
  if (targetElement) {
    observer.unobserve(targetElement);
  }
});

const handleFilter = (filterFormValues: Filter) => {
  toggleSortFilter();
  filterState.filter = filterFormValues;
};

const toggleSortFilter = () => {
  sortFilterOpen.value = !sortFilterOpen.value;
};

const printTotalPrice = (numberLocale: string, currencyCode: string, total: number) => {
  try {
    return formatPrice(total, numberLocale, currencyCode);
  } catch (error) {
    Sentry.captureException(error);
    return currencyCode + total;
  }
};

useSubscribeToReceiptChanges({ receipts, showReceipt });
</script>
