import { useAuthManager } from '@/utils/auth';
import { useIsMutationLoading } from '@/utils/hooks/useIsMutationLoading';
import { useGetCurrentReceipt, useSubscribeToReceipt } from '@/utils/receipt';
import { MutationKeys } from '@pi-xcels/graphql/constants';
import { GetReceiptQuery, UserReceiptsQuery } from '@pi-xcels/graphql/types';
import clone from 'just-clone';
import { Ref, computed, watch } from 'vue';

export function useSubscribeToReceiptChanges({
  receipts,
  showReceipt,
}: {
  receipts: Readonly<Ref<UserReceiptsQuery['userReceipts']>>;
  showReceipt: (payload: {
    data: NonNullable<UserReceiptsQuery['userReceipts']>[number] | NonNullable<GetReceiptQuery['getReceipt']>;
    isNewReceipt?: boolean;
    viewOnly?: boolean;
  }) => void;
}) {
  const { isAuthenticated, isAnonymous } = useAuthManager();
  const isClearingCurrentReceipt = useIsMutationLoading([MutationKeys.ClearCurrentReceipt]);
  const { latestReceiptSubscription } = useSubscribeToReceipt({
    // subscribe to incomplete receipts
    transactionRefs: computed(() =>
      receipts.value
        ?.filter((receipt) => receipt.state !== 'COMPLETE')
        ?.map((receipt) => receipt.transactionRef ?? '')
        .filter(Boolean)
    ),
    enabled: computed(() => isAuthenticated.value && !isAnonymous.value && !isClearingCurrentReceipt.value),
  });

  const { data: currentReceiptData } = useGetCurrentReceipt({
    isAuthenticated,
    isAnonymous,
    transactionRef: computed(() => {
      // fetch receipt from transactionRef if it is complete
      if (latestReceiptSubscription.value?.state === 'COMPLETE') return latestReceiptSubscription.value.transactionRef || '';
      return '';
    }),
    enabled: computed(() => !isClearingCurrentReceipt.value),
  });

  const currentReceipt = computed(() => {
    if (!currentReceiptData.value?.getReceipt) return;
    return clone(currentReceiptData.value?.getReceipt);
  });

  const canShowReceiptFromSubs = computed(
    () =>
      !!Object.keys(currentReceipt.value || {}).length &&
      (!currentReceipt.value?.isBinaryReceipt || (currentReceipt.value.isBinaryReceipt && !!currentReceipt.value.rawReceiptBinary))
  );

  //Refetch receipt from receipt subscription
  watch([currentReceipt, canShowReceiptFromSubs], ([currentReceipt, canShowReceiptFromSubs]) => {
    if (currentReceipt && canShowReceiptFromSubs) {
      showReceipt({
        data: currentReceipt,
        isNewReceipt: true,
      });
    }
  });
}
